body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  max-width: 800px;
  margin: 0 auto;
}

.App-nav {
  margin: 20px;
  padding: 10px;
  border-bottom: 2px solid #508FCA;
}

.App-nav ul {
  padding: 0;
  margin: 0;
}

.App-nav-item {
  list-style: none;
  display: inline-block;
  margin-right: 32px;
  font-size: 24px;
  border-bottom: 4px solid transparent;
}

.App-content {
  margin: 0 20px;
}

.App-nav-item a {
  cursor: pointer;
}

.App-nav-item a:hover {
  color: #999;
}

.App-nav-item.selected {
  border-bottom-color: #FFAA3F;
}
.Item {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #ccc;
    padding: 10px;
}
.Item-image {
    width: 64px;
    height: 64px;
    border: 1px solid #ccc;
    margin-right: 10px;
    float: left;
}
.Item-title {
    font-weight: bold;
    margin-bottom: 0.4em;
}
.Item-price {
    text-align: right;
    font-size: 18px;
    color: #555;
}
.Item-addToCart {
    margin-bottom: 5px;
    font-size: 14px;
    border: 2px solid #FFAA3F;
    background-color: #fff;
    border-radius: 3px;
    cursor: pointer;
}
.Item-addToCart:hover {
    background-color: #FFDDB2;
}
.Item-addToCart:active {
    background-color: #ED8400;
    color: #fff;
    outline: none;
}
.Item-addToCart:focus {
    outline: none;
}
.Item-left {
    flex: 1 1;
}
.Item-right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.ItemPage-items {
    margin: 0;
    padding: 0;
}
.ItemPage-items li {
    list-style: none;
    margin-bottom: 20px;
}

.CartPage-items {
    margin: 0;
    padding: 0;
}
.CartPage-items li {
    list-style: none;
}
.CartItem-count {
    padding: 5px 10px;
    border: 1px solid #ccc;
}
.CartItem-addOne,
.CartItem-removeOne {
    padding: 5px 10px;
    border: 1px solid #ccc;
    background: #fff;
}
.CartItem-addOne {
    border-left: none;
}
.CartItem-removeOne {
    border-right: none;
}
